.header {
    &__row {
        display: flex;
        flex-direction: column-reverse; }
    &__content {
        max-width: 670px; }
    &__title {
        margin: 0;
        margin-top: 15px;
        font-family: "Playfair Display", serif;
        font-weight: normal;
        font-size: 32px;
        line-height: 1.3; }
    &__text {
        font-size: 16px;
        line-height: 1.5;
        margin: 20px 0 20px 0; }
    &__text p {
        margin: 0; } }

/* Для Десктопа */
@media (min-width: 540px) {
    .header {
        &__row {
            position: relative;
            display: flex;
            justify-content: flex-end;
            flex-direction: row; }
        &__content {
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            max-width: 65%; }
        &__photo {
            display: block;
            max-width: 62%; }
        &__text {
            font-size: 22px;
            margin: 20px 0 35px 0; } } }

@media (min-width: 992px) {
    .header {
        &__content {
             max-width: 55%; }
        &__title {
            font-size: 72px; }
        &__text {
            font-size: 22px;
            margin: 20px 0 60px 0; } } }
